import React from "react";
import { Link } from "react-router-dom";

import Navigation from "./Navigation.js";
import Editor from "./Editor.js";

import styles from "./css/Home.module.scss";

import pyimg from "./images/Home/python.png";
import jsimg from "./images/Home/javascript.png";

class Home extends React.Component {
  render() {
    return (
      <div className={styles.body}>
        <div className={styles.jssplash}>
          <img className={styles.jssplash} src={jsimg} alt="JS Icon" />
          <div className={styles.jssplashtext}>
            <Link to="/projects/js">
              This very website was built <br /> using{" "}
              <span className={styles.under}>JavaScript!</span>
            </Link>
          </div>
        </div>
        <div className={styles.pysplash}>
          <img className={styles.pysplash} src={pyimg} alt="Python Icon" />
        </div>
        <div className={styles.nav}>
          <Navigation />
        </div>
        <div className={styles.editorpadding}>
          <div className={styles.editor}>
            <Editor selector={true}>
              {"function Welcome(user) {\n"}
              {
                '  console.log("Hello! My name is Arthur Zhang and this is my website!")\n'
              }
              {"}\n"}
              {"\nFeel free to look around!\n\n"}
              {
                "This editor is interactive and is used to display my projects!\n\n"
              }
              {"Have a look at the navbar too!"}
            </Editor>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
