import React from "react";
import Misbehave from "misbehave";
import Prism from "prismjs";
import SimpleBar from "simplebar";
import Select from "react-select";

import "simplebar/dist/simplebar.min.css";
import styles from "./css/Editor.module.scss";

import "./css/barmod.css";
import "./css/prism.css";
require("prismjs/components/prism-markup.js");
require("prismjs/components/prism-markdown.js");
require("prismjs/components/prism-c.js");
require("prismjs/components/prism-cpp.js");
require("prismjs/components/prism-java.js");
require("prismjs/components/prism-jsx.js");
require("prismjs/components/prism-python.js");
require("prismjs/components/prism-json.js");
require("prismjs/components/prism-jsonp.js");
require("prismjs/components/prism-arduino.js");
require("prismjs/components/prism-yaml.js");
require("prismjs/components/prism-scss.js");

class Editor extends React.Component {
  constructor(props) {
    super();
    this.Selector = this.Selector.bind(this);
    this.state = {
      showMenu: false,
      lang: { value: "javascript", label: "JavaScript" },
      codeWidth: 0
    };

    this.showMenu = this.showMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
    this.changeLang = this.changeLang.bind(this);
  }
  componentDidMount() {
    let editor = new Misbehave(
      document.getElementsByClassName(styles.code)[0],
      {
        autoIndent: true,
        autoOpen: true,
        autoStrip: true,
        overwrite: true,
        softTabs: 2,
        replaceTab: true,
        pairs: [["(", ")"], ["[", "]"], ["{", "}"], ['"'], ["'"]],
        oninput: () => {
          Prism.highlightElement(
            document.getElementsByClassName(styles.code)[0]
          );
        },
        undoLimit: 0
      }
    );
    const simpleBar = new SimpleBar(
      document.getElementsByClassName(styles.editor)[0]
    );
    this.bar = simpleBar;
  }
  componentDidUpdate(prevProps) {
    if (prevProps.lang !== this.state.lang) {
      Prism.highlightElement(document.getElementsByClassName(styles.code)[0]);
    }

    console.log(this.bar);
  }
  render() {
    return (
      <div className={styles.body}>
        <div className={styles.extended} />
        <div className={styles.coder}>
          <div id={styles.code} />
          <div className={styles.editor} data-simplebar>
            <pre
              className={
                "language-" + this.state.lang.value + " " + styles.precode
              }
            >
              <code
                className={
                  "language-" + this.state.lang.value + " " + styles.code
                }
                contentEditable={true}
                autoCorrect="off"
                autoCapitalize="off"
                spellCheck="false"
                onInput={e => {
                  this.addText(e);
                }}
                onKeyUp={e => {
                  this.addText(e);
                }}
                onKeyDown={e => {
                  this.addText(e);
                }}
                onBlur={e => {
                  this.addText(e);
                }}
              >
                {this.props.children}
              </code>
            </pre>
          </div>
          <this.Selector />
        </div>
      </div>
    );
  }
  addText(e) {
    this.bar.recalculate();
  }
  Selector() {
    var { lang } = this.state;
    console.log(lang);
    if (this.props.selector) {
      return (
        <div className={styles.selector}>
          <Select
            isSearchable={true}
            placeholder={"Language"}
            value={lang}
            onChange={this.changeLang}
            options={[
              { value: "javascript", label: "JavaScript" },
              { value: "html", label: "HTML" },
              { value: "css", label: "CSS" },
              { value: "jsx", label: "JSX" },
              { value: "java", label: "Java" },
              { value: "py", label: "Python" },
              { value: "c", label: "C" },
              { value: "cpp", label: "C++" },
              { value: "arduino", label: "Arduino" },
              { value: "markup", label: "Markup" },
              { value: "clike", label: "C Like" },
              { value: "json", label: "JSON" },
              { value: "jsonp", label: "JSONP" },
              { value: "markdown", label: "Markdown" },
              { value: "scss", label: "SCSS" },
              { value: "yml", label: "YAML" }
            ]}
          />
        </div>
      );
    }
    return null;
  }
  changeLang(lang) {
    this.setState({ lang: lang });
  }
  closeMenu(event) {
    if (!this.dropdownMenu.contains(event.target)) {
      this.setState({ showMenu: false }, () => {
        document.removeEventListener("click", this.closeMenu);
      });
    }
  }
  showMenu(event) {
    event.preventDefault();

    this.setState({ showMenu: true }, () => {
      document.addEventListener("click", this.closeMenu);
    });
  }
}

export default Editor;
