import React from "react";
import { Link } from "react-router-dom";

import styles from "./css/Error.module.scss";

import err404 from './images/Error/404.png';

class Error extends React.Component {
  render() {
    if (this.props.err === "404") {
      return (
        <div className={styles.body}>
          <div className={styles.center}>
            <img className={styles.fof} src={err404} />
            <br />
            <span className={styles.text}>
              You should never reach this page... because it doesn't exist!
            </span>
            <br />
            <br />
            <button className={styles.return} >
              <Link to="/"> Return to Home </Link>
            </button>
          </div>
        </div>
      );
    }
    return null;
  }
}

export default Error;
